import React from 'react';
import {Button, FormItem, Input, Select, Title} from "@vkontakte/vkui";
import {getUserMock} from "../../../modulesStore/actions/user";
import "../style.scss";

const FormComponent = props => {
    const {handleChangeData, handleSubmit} = props;
    return <form onSubmit={e => {
        e.preventDefault();
        handleSubmit();
    }}>
        <FormItem>
            <Title level={"2"}> </Title>
        </FormItem>
        <FormItem top=" ">
            <Select
                onChange={e => {
                    handleChangeData('type', e.target.value)
                }}
                options={[
                    {
                        value: "buy",
                        label: "  ",
                    },
                    {
                        value: "sell",
                        label: "  ",
                    },
                ]}
            />
        </FormItem>
        <FormItem top=" ">
            <Input placeholder={' '} type='number' onChange={e => {
                handleChangeData('quantity', e.target.value)
            }}/>
        </FormItem>
        <FormItem top="  ">
            <Input placeholder={'    '} type='number' onChange={e => {
                handleChangeData('price', e.target.value)
            }}/>
        </FormItem>
        <FormItem>
            <Button stretched={true} size={'l'} type='submit'> </Button>
        </FormItem>
    </form>
}

export default FormComponent;
