import React from 'react';
import {Button, FormItem, Input, Text, Title} from "@vkontakte/vkui";
import "../style.scss"

const DealInfoComponent = props => {
    const {handleChangeData, handleSubmit, resultPrice, deal} = props;

    return <form onSubmit={e => {
        e.preventDefault();
        handleSubmit();
    }}>
        <FormItem>
            <Title level={"2"}>{deal?.type === "buy"? "": ""}    <b className={"NameUserDeal"}>{deal?.name}</b></Title>
        </FormItem>
        <FormItem>
            <Title level={"4"}> </Title>
        </FormItem>
        <div className={"inputField"}>
            <FormItem top=" ">
                <Input placeholder={' '} type='number' onChange={e => {
                    handleChangeData('quantity', e.target.value)
                }}/>
            </FormItem>
            <FormItem top="  ">
                <Input placeholder={' '} type='number' onChange={e => {
                    handleChangeData('price', e.target.value)
                }}/>
            </FormItem>
        </div>
        <FormItem>
            <Text weight={"regular"}>  : <b>{resultPrice}</b></Text>
        </FormItem>
        <FormItem>
            <Text weight={"regular"}>   : <b>{deal?.phone}, {deal?.name}</b></Text>
        </FormItem>
        <FormItem>
            <Button stretched={true} size={'l'} type='submit'> </Button>
        </FormItem>
    </form>
}

export default DealInfoComponent;